.vczapi-blocks-form {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  width: 100%; }
  .vczapi-blocks-form--group {
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    margin-bottom: 1.5em; }
  .vczapi-blocks-form .radio-inline, .vczapi-blocks-form .toggle-inline {
    width: 100%; }
    .vczapi-blocks-form .radio-inline .components-base-control__field, .vczapi-blocks-form .toggle-inline .components-base-control__field {
      display: flex;
      align-content: center;
      width: 100%; }
      .vczapi-blocks-form .radio-inline .components-base-control__field .components-base-control__label, .vczapi-blocks-form .toggle-inline .components-base-control__field .components-base-control__label {
        flex: 1 1 auto; }
      .vczapi-blocks-form .radio-inline .components-base-control__field label, .vczapi-blocks-form .toggle-inline .components-base-control__field label {
        margin-right: 10px; }
  .vczapi-blocks-form .toggle-inline {
    display: flex;
    flex-direction: row-reverse; }
  .vczapi-blocks-form .range-control {
    width: 100%; }
  .vczapi-blocks-form .text-input {
    width: 100%; }
    .vczapi-blocks-form .text-input .components-base-control__field,
    .vczapi-blocks-form .text-input .components-base-control__label,
    .vczapi-blocks-form .text-input .components-base-control__input {
      width: 100%; }
  .vczapi-blocks-form--input-label {
    width: 100%; }
  .vczapi-blocks-form--select {
    flex: 1 1 auto;
    margin: 0 8px 0 0; }
  .vczapi-blocks-form--selected-meeting {
    width: 100%;
    margin-bottom: 2em;
    border-bottom: 1px solid; }
  .vczapi-blocks-form .vczapi-blocks-pagination {
    width: 100%;
    display: flex;
    /* align-items: center; */
    justify-content: center;
    margin-top: 20px; }
    .vczapi-blocks-form .vczapi-blocks-pagination button.selected {
      background: var(--wp-admin-theme-color);
      color: #fff; }
